.button {

    cursor: pointer;
    font: inherit;
    border: none;
    background-color: #ffc72c;
    color: whitesmoke;
   padding: 0.75rem 3rem;
   display: flex;
   justify-content: space-around;
   align-items: center;
   border-radius: 25px;
   font-weight: bold;
   text-transform: uppercase;
}

.button:hover,
.button:active {

    background-color: #2c0d00;
    color:#ffc72c;
}

.badge {

    background-color: #b94517;
    padding: 0.25rem 1rem;
    border-radius: 25px;
    margin-left: 1rem;
    font-weight: bold;
}

.button:hover .badge,
button:active .badge {

    background-color: #92320c;
}

.bump {

    animation: bump 300ms ease-out;
}

@keyframes bump {

    0% {

        transform: scale(1);
    }

    10% {

        transform: scale(0.9);
    }

    30% {

        transform: scale(1.1);
    }

    50% {

        transform: scale(1.15);
    }

    100% {

        transform: scale(1);
    }
}