.cart-items {

    list-style: none;
    margin: 0;
    padding: 0;
    max-height: 10rem;
    overflow: auto;
}

.total {

    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-size: 1.5rem;
    margin: 1rem 0;
}

.actions {

    text-align: right;
}

.actions button {

    font: inherit;
    cursor: pointer;
    background-color: transparent;
    border: 1px solid #2c0d00;
    padding: 0.5rem 2rem;
    border-radius: 20px;
    margin-left: 1rem;
};

.actions button:hover,
.actions button:active {
  background-color: #ffc72c;
  border-color: #ffc72c;
  color: white;
}

.actions .button--alt {
  color: #2c0d00;
}

.actions .button {
  background-color: #2c0d00;
  color: white;
}
