.meal {

    display: flex;
    justify-content: space-between;
    margin: 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid #ccc;
}

.meal h3 {

    margin: 0 0 0.25rem 0;
}

.description {

    font-style: oblique;
}

.price {

    margin-top: 0.25rem;
    font-weight: bold;
    color:#b94517;
    font-size: 1.25rem;
}